export default {
	data(){
		return{
			categoryList:[],
			subCategoryList: [],
			height:0,
			categoryActive:0,
			categorySubActive:0,
			scrollTop:0,
			scrollHeight:0,
			repairObj:{
				bigID:'',//第一级的id
				subID:'',//第二季的id
				bigName:'',
				subName:''
			},
			list:[]

		}
	},
	methods:{
		goBack(){
			uni.reLaunch({
				url:'../record-shop/record-shop?bigID='+this.repairObj.bigID+'&bigName='+this.repairObj.bigName+"&subID="+this.repairObj.subID+"&subName="+this.repairObj.subName
			})
		},
		scroll(e){
			this.scrollHeight = e.detail.scrollHeight;
		},
		categoryClickMain(categroy, index) {
			this.categoryActive = index;
			this.categorySubActive=0;//右边默认为第一个
			this.subCategoryList = categroy.subCategoryList;
			this.scrollTop = -this.scrollHeight*index;
			if(this.subCategoryList.length){
				this.repairObj.subID=this.subCategoryList[0].obj.id;
				this.repairObj.subName=this.subCategoryList[0].obj.val;
			}
			

		},
		categoryClickSub(categroy, index) {
			this.categorySubActive = index;
			this.scrollTop = -this.scrollHeight*index;
			this.repairObj.subID=this.subCategoryList[index].obj.id;
			this.repairObj.subName=this.subCategoryList[index].obj.val;

		},
		getCategory() {
			var _this=this;
			uni.request({
				url:this.$store.state.url+'Constants',
				data:{
					owner:8,
					type:this.$store.state.constants.brand_type,
					parent:-1,
					objects:1
				},
				success: (res) => {
					this.list=res.data.data
					var bigArray=[],subArray=[];
					res.data.data.forEach(item=>{
						if(item.parent==0){
							bigArray.push(item)
						}else{
							subArray.push(item);
						}
					})
					for(var i=0;i<bigArray.length;i++){
						var subList = [];
						for(var j=0;j<subArray.length;j++){
							if(subArray[j].parent==bigArray[i].id){
								subList.push({"NAME":subArray[j].val,obj:subArray[j]})
							}
						}
						this.categoryList.push({"NAME":bigArray[i].val,"subCategoryList":subList,obj:bigArray[i]})
					}
				
					this.categoryList[0].subCategoryList.unshift({"NAME":'选择品牌类别',obj:{}})
					this.subCategoryList = this.categoryList[0].subCategoryList;
					this.repairObj.bigID=this.categoryList[0].obj.id;
					this.repairObj.bigName=this.categoryList[0].obj.val;
				}
			})

		}
	},
	onLoad:function(){
		this.getCategory()
		uni.getSystemInfo({
			success: res => {
				this.height = res.screenHeight;
			}
		})
	}
}
